.debug {
  outline: 1px solid red;
}

.timer-text {
  color: white;
  font-size: 3rem;
  justify-content: center;
  font-weight: bold;
}

.title-text {
  color: white;
  font-size: 5vh;
  justify-content: center;
  align-self: center;
  font-weight: 500;
}

.navbar {
  display: flex;
  background-color: var(--primary-blue);
  border-color: var(--primary-blue);
  padding: 0 4rem;
}

.nav-side {
  display: flex;
  flex: 3;
}

.nav-main {
  display: flex;
  flex: 5;
  justify-content: center;
}

.circle {
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 4rem;
  background-color: var(--primary-blue);
  border-radius: 50%;
  color: #ffffff;
}

.help{
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 7rem;
}

.helpShadow{
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 7rem;
}

.arrow {
  display: flex;
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 5rem;
}

.paragraph-text {
  color: black;
  font-weight: 500;
  font-size: clamp(1rem, 1.67vmax, 2rem);
  line-height: 5.0;
  tab-size: 8;
  width: 68%;
  text-align: justify;
}

.round-box-ans {
  color: #ffffff;
  background-color: initial; /* If background-color is inherited or unset */
  white-space: nowrap;
  transform: translate(-50%, -50%);
  font-weight: 500;
  font-size: clamp(1rem, 1.67vmax, 2rem);
}

.round-box {
  display: inline;
  font-weight: 500;
  font-size: clamp(1rem, 1.67vmax, 2rem);
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-align: start;
  text-decoration: none;
  color: var(--default-gray);
  background-color: var(--default-gray);
  border: none;
  border-radius: clamp(0.5rem, 0.835vmax, 1rem);
  user-select: none;
  max-width: max-content;
  padding: clamp(0rem, 2.505vmax, 3rem) 0.5rem;
}

.ans-text {
  display: inline-block;
  font-weight: 500;
  align-items: center;
  justify-content: center;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #ffffff;
}

button.submit {
  padding: 1rem 2rem;
  font-size: 2rem;
  font-weight: bold;
  background-color: var(--default-gray);
  box-shadow: 0 1rem var(--default-shadow);
  transform: translateY(-11px);
}

.button.submit:active {
  box-shadow: 0 5px var(--default-gray-emphasis);
  transform: translateY(0px);
}

button.red {
  background-color: var(--submit);
  box-shadow: 0 1rem var(--submit-shadow);
}

button.red:hover {
  background-color: var(--submit-dark);
}

.button.red:active {
  background-color: var(--submit-dark);
  box-shadow: 0 5px var(--submit-shadow);
}

button.green {
  background-color: var(--confirm);
  box-shadow: 0 1rem var(--confirm-shadow);
}

button.green:hover {
  background-color: var(--confirm-dark);
}

.button.green:active {
  background-color: var(--confirm-dark);
  box-shadow: 0 5px var(--confirm-shadow);
}

.main-content {
  flex: 5;
  display: flex;
  flex-direction: column;
}

input[type="text"] {
  width: 30rem;           /* Adjust width as needed */
  height: 5rem;           /* Adjust height as needed */
  padding: 10px;          /* Add padding for better spacing */
  font-size: 32px;        /* Increase font size */
  border: 1px solid #ccc; /* Optional: border styling */
  border-radius: 1rem;     /* Optional: rounded corners */
  outline: none;
  /* Optional: more styles */
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Optional: shadow */
  display: flex;
}

.wrapper {
  display: flex;               /* Use Flexbox */
  justify-content: center;     /* Center horizontally */
  flex-direction: row;
  align-items: center;         /* Center vertically */
  height: 100vh;              /* Full viewport height */
  width: 100vw;                /* Full viewport width */
}

.input-button-container {
  display: flex;
  align-items: center; /* Align items vertically centered */
}

.input-button-container input {
  margin-right: 2rem; /* Add space between input and button */
}

.tri-button {
  display: flex;
}