.circle {
  display: flex; /* Use flexbox */
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 4rem;
  font-weight: bold;
  background-color: var(--primary-blue);
  border-radius: 50%;
  color: #ffffff;
}

.help{
  display: flex; /* Use flexbox */
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 7rem;
}

.helpShadow{
  display: flex; /* Use flexbox */
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 7rem;
}

.arrow {
  display: flex; /* Use flexbox */
  align-items: center; /* Center vertically */
  justify-content: center;
  width: 7rem;
  height: 7rem;
  font-size: 5rem;
}

.button {
  display: flex; /* Use flexbox */
  align-items: center; /* Center vertically */
  justify-content: center;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  outline: none;
  color: #ffffff;
  background-color: var(--default-gray);
  border: none;
  border-radius: 1rem;
  box-shadow: 0 16px var(--default-shadow);
  padding: 0.5rem 2rem;
}

.button:hover {background-color: var(--default-gray-emphasis)}

.button:active {
  background-color: var(--default-gray-emphasis);
  box-shadow: 0 5px var(--default-shadow);
  transform: translateY(11px);
}

button.audio {
  width: 16rem;
  height: 16rem;
  font-size: 5.625rem;
  border-radius: 50%;
  box-shadow: 0 16px var(--default-shadow);
}

button.confirm {
  padding: 0.5rem 2rem;
  font-size: 4rem;
  border-radius: 1rem;
}

.button.confirm:active {
  transform: translateY(11px);
}

button.green {
  background-color: var(--confirm);
  box-shadow: 0 1rem var(--confirm-shadow);
}

button.green:hover {
  background-color: var(--confirm-dark);
}

.button.green:active {
  background-color: var(--confirm-dark);
  box-shadow: 0 5px var(--confirm-shadow);
}

button.red {
  background-color: var(--submit);
  box-shadow: 0 1rem var(--submit-shadow);
}

button.red:hover {
  background-color: var(--submit-dark);
}

.button.red:active {
  background-color: var(--submit-dark);
  box-shadow: 0 5px var(--submit-shadow);
}

.main-content {
  flex: 5;
  display: flex;
  flex-direction: column;
}

.side-column {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.left-column {
  justify-content: flex-start;
}

.right-column {
  justify-content: center;
}

input[type="text"] {
  width: 30rem;           /* Adjust width as needed */
  height: 5rem;           /* Adjust height as needed */
  padding: 10px;          /* Add padding for better spacing */
  font-size: 32px;        /* Increase font size */
  border: 1px solid #ccc; /* Optional: border styling */
  border-radius: 1rem;     /* Optional: rounded corners */
  outline: none;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); /* Optional: shadow */
  display: flex;
}

.wrapper {
  display: flex;               /* Use Flexbox */
  justify-content: center;     /* Center horizontally */
  flex-direction: row;
  align-items: center;         /* Center vertically */
  height: 100vh;              /* Full viewport height */
  width: 100vw;                /* Full viewport width */
}

.input-button-container {
  display: flex;
  align-items: center; /* Align items vertically centered */
}

.input-button-container input {
  margin-right: 2rem; /* Add space between input and button */
}

.tri-button {
  display: flex;
  margin: 1rem;
}